mat))) {\n        this.resetValue();\n        return;\n      }\n      let lastDay = new Date(this.parsedDate.year, this.parsedDate.month, 0).getDate();\n  if (this.parsedDate.month === 2) {\n    if (this.parsedDate.date > 29 || (this.parsedDate.date > lastDay && lastDay === 29)) {\n      this.resetValue();\n    }\n  } \n    else if (this.parsedDate.date > lastDay || this.parsedDate.month > 12 ||\n        isNaN(this.parsedDate.date) || isNaN(this.parsedDate.month) || isNaN(this.parsedDate.year)) {\n        this.resetValue();\n      }\n      else if (this.parsedDate.date > lastDay || this.parsedDate.month > 12 ||\n    isNaN(this.parsedDate.date) || isNaN(this.parsedDate.month) || isNaN(this.parsedDate.year)) {\n    this.resetValue();\n  }\n\n  // Artık yıl kontrolü\n  if((this.parsedDate.year.toString()).length == 4)\n  {\n    if (!(this.parsedDate.year % 4 === 0 && (this.parsedDate.year % 100 !== 0 || this.parsedDate.year % 400 === 0))) {\n  if (this.parsedDate.month === 2 && this.parsedDate.date === 29) {\n      this.resetValue();\n    }\n  }\n  }\n    },\n\n    resetValue() {\n      this.isDateChangedBefore = false;\n      this.dateFormatted = \"\";\n      if (!this.isClickClear) {\n        (this.$refs.text as any).reset();\n      }\n      this.internalValue = null;\n      (this as any).qValue = \"\";\n      this.valueInternal = null;\n      this.$emit(\"onClear\");\n    },\n\n    reset() {\n      this.dateFormatted = \"\";\n      (this.$refs.text as any).reset();\n      this.internalValue = null;\n      (this as any).qValue = \"\";\n      this.valueInternal = null;\n    },\n\n    resetValidation() {\n      (this.$refs.text as any).resetValidation();\n    },\n    validationRefs() {\n      return [this.$refs.text];\n    },\n    arraysEqual(a, b) {\n      if (a === b) return true;\n      if (a == null || b == null) return false;\n      if (a.length !== b.length) return false;\n      for (var i = 0; i < a.length; ++i) {\n        if (a[i] !== b[i]) return false;\n     